<template>
  <div>
    <polaris-card :primary-footer-action="{content: 'Generate Report', onAction:generateReport, disabled: disabled}">
    <polaris-card-section>
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th scope="col"><polaris-heading>Order</polaris-heading></th>
                <th scope="col"><polaris-heading>Date</polaris-heading></th>
                <th scope="col"><polaris-heading>Customer</polaris-heading></th>
                <th scope="col"><polaris-heading>Payment</polaris-heading></th>
                <th scope="col"><polaris-heading>Fulfillment</polaris-heading></th>
                <th scope="col"><polaris-heading>Total</polaris-heading></th>
                <th scope="col"><polaris-heading>Fulfilled By</polaris-heading></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(order, index) of orders" :key="order.id">
                <th scope="row"><polaris-heading>{{ order.name }}</polaris-heading></th>
                <td><polaris-display-text>{{ order.created_at }}</polaris-display-text></td>
                <td><polaris-display-text>{{ `${!order.customer ? "" : order.customer.first_name } ${!order.customer ? "" : order.customer.last_name}` }}</polaris-display-text></td>
                <td><polaris-badge status="warning">{{ order.payment_status }}</polaris-badge></td>
                <td><polaris-badge :status="order.fulfillment_status == 'UNFULFILLED' ? 'warning' : ''">{{ order.fulfillment_status }}</polaris-badge></td>
                <td><polaris-display-text>${{ order.total_price }}</polaris-display-text></td>
                <th><polaris-select :options="['Unfulfilled', 'Pathao', 'eCourier', 'Shuderban']" :data-order_id="order.id" :data-fulfillment-id="order.fulfilled_id" :placeholder="order.fulfilled_by == '' ? 'Unfulfilled' : order.fulfilled_by" v-model="fulfilled_by[index]" @change="onFulfillChange($event, order.id, order.fulfillment_id, order.fulfillment_order_id, index)"></polaris-select></th>
              </tr>
            </tbody>
          </table>
        </div>
    </polaris-card-section>
    <polaris-unstyled-link v-if="download.downloadable" :url="download.link" :external="true">
    Download Report
    </polaris-unstyled-link>
  </polaris-card>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  export default {
    data() {
      return {
        download: {
          downloadable: false,
          link: ''
        },
        disabled: false,
        fulfilled_by: [],
        lastOrderId: []
      }
    },
    computed: {
      ...mapGetters([
        'orders'
      ])
    },
    methods: {
      ...mapActions([
        'setSingleOrder'
      ]),
      generateReport() {
        this.download.downloadable = false
        this.$http.get('/generate-report').then(result => {
          this.download.downloadable = true
          this.download.link = result.body.download_link
        })
        .catch(e => {
          console.log(e)
        })
      },
      onFulfillChange(e, order_id, fulfillment_id, fulfillment_order_id, index) {
        this.fulfilled_by[index] = e
        this.disabled = true
        this.download.downloadable = false
        console.log('From Vue', order_id, fulfillment_id)
        this.$http.post('/save', {
          order_id: order_id,
          fulfillment_by: e,
          fulfillment_id: fulfillment_id,
          fulfillment_order_id: fulfillment_order_id
        })
        .then(response => {
          console.log(response.data)
          this.disabled = false
          this.download.downloadable = false
          let newId = null
          if (response.data.orderUpdate.order.fulfillments.length) {
            newId = response.data.orderUpdate.order.fulfillments.find(item => {
              return item.status == 'SUCCESS'
            })
          }

          this.setSingleOrder({
            index: index,
            fulfillment_id: response.data.orderUpdate.order.metafield.id,
            fulfillment_by: response.data.orderUpdate.order.metafield.value,
            payment_status: response.data.orderUpdate.order.displayFinancialStatus,
            fulfillment_status: response.data.orderUpdate.order.displayFulfillmentStatus,
            fulfillment_order_id: !newId ? null : newId.id
          })
        })
        .catch(e => console.log(e))
      }
    }
  }
</script>