import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    orders: []
    // orders: [{
    //   id: 'sasdf',
    //   name: 'asdfads',
    //   created_at: 'adsfasd',

    //   processing_method: 'adfads',
    //   fulfillment_status: 'adfad',
    //   total_price: 'adfads',
    //   customer: {
    //     first_name: 'adfad',
    //   last_name: 'adfads',
    //   },
    //   fulfilled_by: ''
    // },
    // {
    //   id: 'asdfadsf',
    //   name: 'asdfads',
    //   created_at: 'adsfasd',

    //   processing_method: 'adfads',
    //   fulfillment_status: 'adfad',
    //   total_price: 'adfads',
    //   customer: {
    //     first_name: 'adfad',
    //   last_name: 'adfads',
    //   },
    //   fulfilled_by: 'Pathao'
    // }]
  },
  getters: {
    orders: state => {
      return state.orders
    }
  },
  mutations: {
    setOrders: (state, payload) => {
      state.orders = payload
    },
    updateSingleOrder: (state, payload) => {
      if (state.orders[payload.index]) {
        state.orders[payload.index]['fulfillment_by'] = payload.fulfillment_by,
        state.orders[payload.index]['fulfillment_id'] = payload.fulfillment_id,
        state.orders[payload.index]['payment_status'] = payload.payment_status,
        state.orders[payload.index]['fulfillment_status'] = payload.fulfillment_status
        state.orders[payload.index]['fulfillment_order_id'] = payload.fulfillment_order_id
      }
    }
  },
  actions: {
    setOrders: ({ commit }, payload) => {
      commit('setOrders', payload)
    },
    setSingleOrder: ({ commit }, payload) => {
      commit('updateSingleOrder', payload)
    }
  }
})